import { useGetBlogByIdQuery } from '@/services';
import {
  Container,
  Card,
  CardContent,
  Typography,
  CircularProgress,
  Alert,
} from '@mui/material';
import { useRouter } from 'next/router';
import React from 'react';

function BlogDetail() {
  const router = useRouter();
  const { id } = router.query;
  const { data, isLoading, error } = useGetBlogByIdQuery(Number(id));

  if (isLoading) return <Loader/>;
  if (error) return <Alert severity="error">Error loading post!</Alert>;

  return (
    <Container maxWidth="sm" sx={{ mt: 4 }}>
      <Typography variant="h3" align="center" gutterBottom>Blog Detail</Typography>
      
      <Card sx={{ boxShadow: 3, borderRadius: 2 }}>
        <CardContent>
          <Typography variant="h5" gutterBottom>
            {data?.title}
          </Typography>
          <Typography variant="subtitle1" color="text.secondary" gutterBottom>
            By {data?.author} | {new Date(Number(data?.date)).toLocaleDateString()}
          </Typography>
          <Typography variant="body1" sx={{ mt: 2 }}>
            {data?.excerpt}
          </Typography>
        </CardContent>
      </Card>
    </Container>
  );
}

export default BlogDetail;
